@import url('./styles/calcrem.css');
@import url('./styles/variable.css');
@import url('./styles/theme.css');
@import url('./styles/preset.css');
@import url('./styles//animation.css');
@import url('//at.alicdn.com/t/c/font_3329662_ymvhmdawcgf.css');

html {
  color-scheme: light;
}

body {
  margin: 0;
  /* stylelint-disable-next-line font-family-name-quotes */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color);
  font-size: var(--font-size-base);
  background-color: var(--bg-color);
  transition: background-color var(--transition-duration-fast);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div,
p,
h1,
h2,
h3,
h4,
h5,
h6,
table,
caption,
ul,
ol,
dl,
form,
main,
nav,
aside,
header,
section,
footer,
article,
pre,
blockquote,
code,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a,
area,
button,
[role='button'],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

button,
input {
  overflow: visible;
}

html,
body,
div,
main,
nav,
aside,
header,
section,
footer,
article,
pre,
code {
  scrollbar-width: thin;
  scrollbar-color: #0003 transparent;
}

ul,
ol {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #333;
}

img {
  width: 100%;
  height: 100%;
  vertical-align: middle;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  background-color: #0003;
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
}

::selection {
  background-color: #d1edc4;
  color: #fff;
}
